<template>
  <div class="list">
    <div
      class="pageBanner"
      v-if="!pageBanner"
      style="'background:url(../assets/img/pageBanner.jpg)'"
    ></div>
    <div
      class="pageBanner"
      v-else
      :style="'background:url(' + pageBanner + ')'"
    ></div>

    <div class="pageNav">
      <h3>
        <span>{{ pageNavList.Name }}</span>
      </h3>
      <ul v-if="pageNavList.Clist">
        <li v-for="i in pageNavList.Clist" :key="i.ID">
          <hrefformat
            v-if="i.ID == 3987"
            :url="i.Url"
            :param="{ cid: 4001 }"
            :content="i.Name"
          ></hrefformat>
          <hrefformat
            v-else-if="i.ID == 3996"
            :url="i.Url"
            :param="{ cid: 4003 }"
            :content="i.Name"
          ></hrefformat>
          <hrefformat
            v-else
            :url="i.Url"
            :param="{ cid: i.ID }"
            :content="i.Name"
          ></hrefformat>
        </li>
        <!-- <li v-for="i in pageNavList.Clist" :key="i.ID">
          <hrefformat
            :url="i.Url"
            :param="{ cid: i.ID }"
            :content="i.Name"
            
          ></hrefformat>
        </li> -->
      </ul>
      <ul v-else>
        <!-- <p>{{pageNavList.Name}}</p> -->
      </ul>
    </div>
    <div class="pageMain clearfix">
      <router-view :key="$route.fullPath" />
    </div>
  </div>
</template>

<script>
export default {
  name: "list",
  data() {
    return {
      pageCid: "",
      pageFCid: "",
      pageNavList: [],
      pageRouterList: [],
      pageBanner: require("../assets/img/pageBanner.jpg"),
      orderNavName: {},
      nam: "Name",
      navAlldate: [],
    };
  },
  beforeUpdate() {
    var that = this;
    that.navFindF();
  },
  mounted() {
    var that = this;
    that.pageRouterList = that.$route.query;
    
    if (that.pageRouterList.cid) {
      that.pageCid = that.pageRouterList.cid;

      that.navFindF();
    } else {
      that.pageCid = that.Base.GetNewsInfoByNid(that.pageRouterList.id).Fid;
      
      that.navFindF();
    }
    if (!(that.pageNavList.Logo == "http://base.hope55.com")) {
      that.pageBanner = that.pageNavList.Logo;
    }

    that.pageNavList.length != 0 ? that.pageNavList : that.changeNavs();
    // console.log(that.pageNavList,"that.pageNavList233232")
    that.searchtitle(that.pageNavList.Fid);
  },
  methods: {
    Navformat: function (data) {
      var obj = data;

      var temp = [];
      for (var i = 0; i < obj.length; i++) {
        if (obj[i].FID == "0") {
          var main = obj[i];
          main.Clist = [];
          for (var j = 0; j < obj.length; j++) {
            if (obj[j].FID == main.ID) {
              main.Clist.push(obj[j]);
            }
          }
          temp.push(main);
        }
      }

      return temp;
    },
    navFindF: function (id) {
      var that = this;
      var navList = that.Navformat(that.Base.GetNavColumnsByDid());
      // console.log(navList,"navlist")

      // console.log(that.Base.GetColumnByid(that.pageCid),"///")
      // console.log(that.Base.GetColumnByid(that.pageCid).Fid)


      if (that.Base.GetColumnByid(that.pageCid).Fid) {
        that.pageFCid = that.Base.GetColumnByid(that.pageCid).Fid;
        // console.log(that.pageFCid,"that.pageFCid")
        // console.log(navList.filter(item=>item.ID == that.pageFCid))

        // console.log(that.pageFCid,"that.pageFCid")

        for (let key in navList) {
          if (navList[key].ID == that.pageFCid) {
            // console.log(navList[key],"navList[key]")
            that.pageNavList = navList[key];
          } else {
            if (navList[key].Clist) {
              for (let i = 0; i < navList[key].Clist.length; i++) {
                if (navList[key].Clist[i].ID == that.pageFCid) {
                  that.pageNavList = navList[key].Clist[i];
                  that.pageNavList.Clist = that.Base.GetCNavColumnsByCid(
                    navList[key].Clist[i].ID
                  );
                  // console.log(
                  //   (that.pageNavList.Clist = that.Base.GetCNavColumnsByCid(
                  //     navList[key].Clist[i].ID
                  //   )),
                  //   "2555"
                  // );
                  // console.log(navList[key].Clist[i], "2255");
                }
              }
            }
          }
        }
      } else {
        for (let key in navList) {
          if (navList[key].ID == that.pageCid) {
            that.pageNavList = navList[key];
          } else {
            that.pageNavList = that.Base.GetColumnByid(that.pageCid);
            console.log(that.pageNavList, "///22222");
          }
        }
      }
    },
    GetColumnByid: function (cid) {
      var that = this;
      return that.Base.GetColumnByid(cid);
    },
    changeNavs: function () {
      var that = this;
      // if (that.pageRouterList.cid) {
      //   that.pageNavList = that.Base.GetColumnByid(
      //     Number(that.pageRouterList.cid)
      //   );
      // }else{
      //   that.pageNavList = that.Base.GetColumnByid(
      //     Number(that.pageRouterList.id)
      //   );
      // }
      that.pageNavList = that.pageRouterList.cid?that.pageNavList = that.Base.GetColumnByid(
          Number(that.pageRouterList.cid)
        ):that.pageNavList = that.Base.GetColumnByid(
          Number(that.pageRouterList.id)
        );

      console.log(that.pageNavList, "/that.pageNavList222");
      // console.log(that.Base.GetColumnByid(Number(that.pageRouterList.Fid))
      // console.log(that.Base.GetCNavColumnsByCid(Number(that.pageNavList.Fid)));
      // console.log( that.Base.GetANavColumnsByCid(that.pageNavList.Fid));
      // console.log(
      //   that.Base.GetFNavColumnsByCid(Number(that.pageRouterList.Fid))
      // );
    },
    searchtitle: function (cid) {
      var _this = this;
      var ty = [];
      if (_this.pageNavList.Fid) {
        _this.pageNavList = _this.Base.GetColumnByid(cid);
        _this.pageNavList.Clist = _this.Base.GetCNavColumnsByCid(cid);
      }
    },
  },
};
</script>

<style scoped>
.list {
  width: 100%;
  overflow: hidden;
  padding-bottom: 40px;
}
.pageBanner {
  width: 100%;
  /* height: 300px; */
  height: 400px;
  background-image: url(../assets/img/banner.jpg);
  background-repeat: no-repeat !important;
  background-size: cover !important;
  background-position: center !important;
}
.pageNav {
  width: 1200px;
  height: 50px;
  background-color: #f7f7fa;
  margin: 0 auto;
  margin-top: 40px;
}
.pageNav h3 {
  float: left;
  /* width: 260px; */
  height: 50px;
  line-height: 50px;
  text-indent: 82px;
  font-size: 24px;
  color: white;
  position: relative;
  padding: 0 40px 0 220px;
  /* border: 1px solid red; */
}
.pageNav h3::before {
  content: "";
  width: 220px;
  height: 70px;
  bottom: 0px;
  left: 0px;
  position: absolute;
  background-color: #4273ff;
}
.pageNav h3::after {
  content: "";
  width: 0px;
  height: 0px;
  bottom: 0px;
  right: 0px;
  position: absolute;
  border-left: solid 20px #4273ff;
  border-bottom: solid 35px #4273ff;
  border-right: solid 20px transparent;
  border-top: solid 35px transparent;
}
.pageNav h3 span {
  /* display: block; */
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0px;
  top: 0px;
  z-index: 3;
  box-sizing: border-box;
  /* padding: 0px 10px; */
}
.pageNav ul {
  float: right;
  width: 900px;
  display: flex;
}
.pageNav ul li {
  flex: 1;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.pageNav ul li a {
  font-size: 18px;
  color: #333333;
  text-align: center;
  line-height: 1.2;
}
.pageNav ul li a.router-link-active,
.pageNav ul li:hover a {
  color: #4273ff;
}
.pageMain {
  width: 1200px;
  margin: 0 auto;
  margin-top: 50px;
  padding-top: 1px;
}
</style>



