<template>
	<div class="mlxy">
		<div class="mlxy-row xyfg">
			<div class="mlxy-head">校园风光</div>
			<swiper :options="swiperOption" class="xyfg-item">
				<swiper-slide v-for="(item,index) in mlxyList.xyfgList" v-if="item.length==5" :key="index" class="swiper-slide xyfg-item-list">
					<div class="fl">
						<dd class="imgBox"><img :src="item[0].FileUrl" alt=""></dd>
						<dd class="imgBox"><img :src="item[1].FileUrl" alt=""></dd>
					</div>
					<div class="center">
						<dd class="imgBox"><img :src="item[2].FileUrl" alt=""></dd>
					</div>
					<div class="fr">
						<dd class="imgBox"><img :src="item[3].FileUrl" alt=""></dd>
						<dd class="imgBox"><img :src="item[4].FileUrl" alt=""></dd>
					</div>
				</swiper-slide>
				<div class="swiper-button-prev" slot="button-prev"></div> 
				<div class="swiper-button-next" slot="button-next"></div>				
			</swiper>
		</div>
		<div class="mlxy-row sxsb">
			<div class="mlxy-head">实训设备</div>
			<swiper :options="swiperOption" class="xyfg-item">
				<swiper-slide v-for="(item,index) in mlxyList.sxsbList" v-if="item.length==5" :key="index" class="swiper-slide xyfg-item-list">
					<div class="fl">
						<dd class="imgBox"><img :src="item[0].FileUrl" alt=""></dd>
						<dd class="imgBox"><img :src="item[1].FileUrl" alt=""></dd>
					</div>
					<div class="center">
						<dd class="imgBox"><img :src="item[2].FileUrl" alt=""></dd>
					</div>
					<div class="fr">
						<dd class="imgBox"><img :src="item[3].FileUrl" alt=""></dd>
						<dd class="imgBox"><img :src="item[4].FileUrl" alt=""></dd>
					</div>
				</swiper-slide>
				<div class="swiper-button-prev" slot="button-prev"></div> 
				<div class="swiper-button-next" slot="button-next"></div>				
			</swiper>
		</div>
		<div class="mlxy-row xysh">
			<div class="mlxy-head">校园生活</div>
			<swiper :options="swiperOption" class="xyfg-item">
				<swiper-slide v-for="(item,index) in mlxyList.xyshList" v-if="item.length==5" :key="index" class="swiper-slide xyfg-item-list">
					<div class="fl">
						<dd class="imgBox"><img :src="item[0].FileUrl" alt=""></dd>
						<dd class="imgBox"><img :src="item[1].FileUrl" alt=""></dd>
					</div>
					<div class="center">
						<dd class="imgBox"><img :src="item[2].FileUrl" alt=""></dd>
					</div>
					<div class="fr">
						<dd class="imgBox"><img :src="item[3].FileUrl" alt=""></dd>
						<dd class="imgBox"><img :src="item[4].FileUrl" alt=""></dd>
					</div>
				</swiper-slide>
				<div class="swiper-button-prev" slot="button-prev"></div> 
				<div class="swiper-button-next" slot="button-next"></div>				
			</swiper>
		</div>
	</div>
</template>

<script>
export default {
	name: 'mlxy',
	data() {
		return {
			swiperOption:{
				//设置点击箭头
				nextButton: '.swiper-button-next', 
				prevButton: '.swiper-button-prev',
				//自动轮播
				// autoplay: {
				//   delay: 2000,
				//   //当用户滑动图片后继续自动轮播
				//   disableOnInteraction: false,
				// },
				//开启循环模式
				loop: false
			},			
			mlxyList:{
				xyfgList:[],
				sxsbList:[],
				xyshList:[],
			}
		};
	},
	mounted() {
		var that = this;
		that.mlxyList.xyfgList = that.listFormat(that.api.xyfg,5)
		that.mlxyList.sxsbList = that.listFormat(that.api.sxsb,5)
		that.mlxyList.xyshList = that.listFormat(that.api.xysh,5)
	},
	methods:{
		listFormat:function(id,len){
			var that = this;
			var towerData = [];
			towerData = that.Base.GetFilesInfoByCid('',id,'','',1,'',50).GetData;
			var result = [];
			for(var i=0;i<towerData.length;i+=len){
			    result.push(towerData.slice(i,i+len));
			}
			return result;
		}
	}
};
</script>

<style scoped>
.mlxy{padding-bottom: 10px;}
.mlxy-row{width: 1200px;margin: 0 auto;margin-bottom: 50px;position: relative;}
.mlxy-row:last-child{margin-bottom: 0px;}
.mlxy-head{height: 50px;background: url(../assets/img/mlxy_titleBG.png) center no-repeat;font-size: 24px;line-height: 50px;text-align: center;color: white;margin-bottom: 34px;}
.xyfg-item{width: 100%;height: 400px;position: relative;overflow: hidden;}
.xyfg-item-list{width: 100%;height: 100%;position: absolute;left: 1200px;top: 0px;}
.xyfg-item-list:first-child{left: 0;}
.xyfg-item-list .imgBox{overflow: hidden;}
.xyfg-item-list .imgBox img{transition: all 0.4s;}
.xyfg-item-list .fl{width: 293px;}
.xyfg-item-list .fl .imgBox, .xyfg-item-list .fr .imgBox{width: 293px;height: 195px;overflow: hidden;}
.xyfg-item-list .fl .imgBox:first-child{margin-bottom: 10px;}
.xyfg-item-list .fr .imgBox:first-child{margin-bottom: 10px;}
.xyfg-item-list .center{float: left;width: 600px;height: 400px; margin-left: 7px;}
.xyfg-item-list .fr{width: 293px;}
.xyfg-item-list .imgBox:hover img{transform: scale(1.1);}
.xyfg-btn{position: absolute;top: 250px;left: 0px;width: 100%;height: 0;}
.swiper-button-prev,.swiper-button-next{width: 66px;height: 66px;cursor: pointer; border-radius: 33px;background-color: rgba(0,0,0,0.2);background-image: none; position: absolute;transition: all 0.4s;opacity: 0;color: white;}
.swiper-button-disabled{opacity: 0.5;opacity: 0;}
.mlxy-row:hover .swiper-button-prev{left: 0px;opacity: 1;}
.mlxy-row:hover .swiper-button-next{right: 0px;opacity: 1;}
.swiper-button-prev::after{content: "";width: 100%;height: 100%;position: absolute;left: 0px;top: 0px;background: url(../assets/img/left_btn.png) center no-repeat;}
.swiper-button-next::after{content: "";width: 100%;height: 100%;position: absolute;left: 0px;top: 0px;background: url(../assets/img/right_btn.png) center no-repeat;}

.mlxy-row:hover .swiper-button-disabled{opacity: 0.5;}
</style>



