<template>
  <div class="ztxm" id="towerZtBox">
    <div v-html="ztHtml"></div>
    <!-- 专题路径格式 '/ztxm?ztname=xxx' -->
  </div>
</template>

<script>
var vueStore;
export default {
  name: "ztxm",
  data() {
    return {
      ztConfigJson: {},
      ztHtml: "",
      beforJSTarget: false,
    };
  },
  beforeCreate() {
    let ztname = this.$route.query.ztname;
  },
  created() {
    vueStore = this.$store;
    var that = this;
    let ztname = that.$route.query.ztname;
    function dynamicLoadCss(url) {
      var head = document.getElementsByTagName("head")[0];
      var link = document.createElement("link");
      link.type = "text/css";
      link.rel = "stylesheet";
      link.href = url;
      var myLink =
        '<link href="' +
        url +
        '" type="text/css" rel="stylesheet" scoped><link>';
      $("#towerZtBox").append(myLink);
      // document.getElementById('towerZtBox').appendChild(link);
    }
    function dynamicLoadJs(url, callback) {
      var head = document.getElementsByTagName("head")[0];
      var script = document.createElement("script");
      script.type = "text/javascript";
      script.src = url;
      if (typeof callback == "function") {
        script.onload = script.onreadystatechange = function () {
          if (
            !this.readyState ||
            this.readyState === "loaded" ||
            this.readyState === "complete"
          ) {
            callback();
            script.onload = script.onreadystatechange = null;
          }
        };
      }
      document.body.appendChild(script);
    }
    function removeCSS() {
      for (var i = 0; i < $("link").length; i++) {
        var tempId = $("link").eq(i)[0].href;
        // console.log(tempId);
        if (tempId.indexOf("static/zt") != -1) {
          // console.log(tempId);
          $("link").eq(i).remove();
        }
      }
    }
    function removeJS() {
      for (var i = 0; i < $("script").length; i++) {
        var tempId = $("script").eq(i)[0].src;
        // console.log(tempId);
        if (tempId.indexOf("static/zt") != -1) {
          // console.log(tempId);
          $("script").eq(i).remove();
        }
      }
    }
    removeCSS();
    removeJS();
    $.ajax({
      url: "./static/zt/" + ztname + "/config.json",
      dataType: "JSON",
    }).then((result) => {
      that.ztConfigJson = result;

      $.each(result["beforJS"], function (i, field) {
        // console.log(field);
        dynamicLoadJs(
          "static/zt/" +
            ztname +
            "/" +
            field +
            "?" +
            Math.ceil(Math.random() * 1000)
        );
        // console.log();
        if (result["beforJS"].length - 1 == i) {
          that.beforJSTarget = true;
        }
      });
      $.each(result["css"], function (i, field) {
        // console.log(field);
        dynamicLoadCss("static/zt/" + ztname + "/" + field);
      });
    });
    // $.getJSON('http://localhost:8088'+'/static/zt/' + ztname + '/config.json', function(result) {
    // 	// console.log(result);
    // 	that.ztConfigJson = result;

    // 	$.each(result['beforJS'], function(i, field) {
    // 		// console.log(field);
    // 		dynamicLoadJs('static/zt/' + ztname + '/' + field + '?' + Math.ceil(Math.random() * 1000));
    // 		console.log();
    // 		if (result['beforJS'].length - 1 == i) {
    // 			that.beforJSTarget = true;
    // 		}
    // 	});
    // 	$.each(result['css'], function(i, field) {
    // 		// console.log(field);
    // 		dynamicLoadCss('static/zt/' + ztname + '/' + field);
    // 	});
    // });
  },
  mounted() {
    var that = this;
    let ztname = that.$route.query.ztname;

    that.$root.liststrote = false;

    $.get("static/zt/" + ztname + "/index.html", function (res) {
      var metaJS = [];
      var metaCSS = [];
      var tempHtml = res
        .replace(/img\//g, "./static/zt/" + ztname + "/img/")
        .replace(/images/g, "./static/zt/" + ztname + "/img");

      that.ztHtml = tempHtml;
      function dynamicLoadJs(url, callback) {
        var script = document.createElement("script");
        script.type = "text/javascript";
        script.src = url;
        if (typeof callback == "function") {
          script.onload = script.onreadystatechange = function () {
            if (
              !this.readyState ||
              this.readyState === "loaded" ||
              this.readyState === "complete"
            ) {
              callback();
              script.onload = script.onreadystatechange = null;
            }
          };
        }
        document.body.appendChild(script);
      }
      // console.log(that.beforJSTarget)
      if (that.beforJSTarget) {
        setTimeout(function () {
          $.each(that.ztConfigJson["afterJS"], function (i, field) {
            // console.log(field);
            dynamicLoadJs(
              "static/zt/" +
                ztname +
                "/" +
                field +
                "?" +
                Math.ceil(Math.random() * 1000)
            );
          });
        }, 100);
      }
      $(".ztxm").on("click", ".zxbm", function () {
        vueStore.dispatch("showAddStudent");
      });
      $(".ztxm").on("click", ".zyzx", function () {
        window.open(
          // "http://kab.dalianmilian.com/Web/im.aspx?_=t&accountid=117110",
          that.bdsq,
          "_blank"
        );
      });
      $("#towerZtBox").css("min-height", $(window).height() - 186 - 204);
    });
  },
  destroyed() {
    var _this = this;
    function removeCSS() {
      for (var i = 0; i < $("link").length; i++) {
        var tempId = $("link").eq(i)[0].href;

        if (tempId.indexOf("static/zt") != -1) {
          $("link").eq(i).remove();
        }
      }
    }
    function removeJS() {
      for (var i = 0; i < $("script").length; i++) {
        var tempId = $("script").eq(i)[0].src;

        if (tempId.indexOf("static/zt") != -1) {
          $("script").eq(i).remove();
        }
      }
    }
    removeCSS();
    removeJS();
    _this.$root.liststrote = true;
  },
};
$(function () {});
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
  font-family: 微软雅黑;
  margin: 0px;
  padding: 0;
  border: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
fieldset,
img {
  border: 0;
}
address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}
ol,
ul {
  list-style: none;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}
dl {
  margin-block-start: 0px;
  margin-block-end: 0px;
}
q:before,
q:after {
  content: "";
}
input,
button {
  border: none;
  font-family: "simhei";
}
button {
  background-color: transparent;
  cursor: pointer;
}
input:focus {
  box-shadow: rgba(0, 0, 0, 0.6) 0px 0px 3px;
  -moz-box-shadow: rgba(0, 0, 0, 0.6) 0px 0px 3px;
  -webkit-box-shadow: rgba(0, 0, 0, 0.6) 0px 0px 3px;
}
input,
button:focus {
  outline: none;
}
input,
textarea,
select {
  border: 0; /* 方法1 */
  -webkit-appearance: none; /* 方法2 */
  border-radius: 0;
  outline: none;
}
/* 定位  浮动*/
.tl {
  text-align: left;
}
.tc {
  text-align: center;
}
.tr {
  text-align: right;
}
.bc {
  margin-left: auto;
  margin-right: auto;
}
/* .fl {float:left;display:block;} */
/* 可修复IE6中双倍边距bug */
/* .fr {float:right;display:block;} */
.cb {
  clear: both;
}
.cl {
  clear: left;
}
.cr {
  clear: right;
}
.clearfix:after {
  content: "";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}
.pr {
  position: relative;
}
.pa {
  position: absolute;
}
.zoom {
  zoom: 1;
}
.clear {
  *zoom: 1;
}
.clear:after {
  clear: both;
  content: ".";
  display: block;
  height: 0;
  overflow: hidden;
  visibility: hidden;
  zoom: 1;
}
.none {
  display: none;
}
/* .imgBox img{display: block;width: 100%;height: 100%;} */
/*@功能：全局css定义*/
body {
  font-size: 16px;
  color: #333333;
  font-family: 微软雅黑;
  min-width: 1200px;
}
a {
  color: inherit;
  text-decoration: none;
}
/*去除input的默认样式  number的箭头*/ /*在chrome下：*/
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none !important;
  margin: 0;
}
/*Firefox下：*/
input[type="number"] {
  -moz-appearance: textfield;
}
body {
  max-width: 1920px;
  min-width: 1200px;
  margin: 0 auto;
}
#towerZtBox {
  overflow: hidden;
  min-height: 500px;
}
</style>



