<template>
  <div class="datefontlist">
    <div class="pageCont">
      <ul>
        <li v-for="i in pageList" :key="i.ID">
          <template v-if="Curren.isEmpty(i.Url)">
            <router-link :to="{ path: '/list/content', query: { id: i.ID } }">
              <div class="date">
                <b>{{ Curren.formatDatebox(i.ShowTime, "", 2) }}</b
                ><span>{{ Curren.formatDatebox(i.ShowTime, "-", 6) }}</span>
              </div>
              <div class="font">
                <h3>
                  <span>{{ i.Title }}</span>
                </h3>
                <p>{{ i.Describe }}</p>
              </div>
            </router-link>
          </template>
          <template v-else>
            <a :href="i.Url" target="_blank">
              <div class="date">
                <b>{{ Curren.formatDatebox(i.ShowTime, "", 2) }}</b
                ><span>{{ Curren.formatDatebox(i.ShowTime, "-", 6) }}</span>
              </div>
              <div class="font">
                <h3>
                  <span>{{ i.Title }}</span>
                </h3>
                <p>{{ i.Describe }}</p>
              </div>
            </a>
          </template>
        </li>
        <!-- 				<li v-for="i in pageList" v-else><a :href="i.Url">
					<div class="date"><b>{{ Curren.formatDatebox(i.ShowTime,"",2) }}</b><span>{{ Curren.formatDatebox(i.ShowTime,"-",6) }}</span></div>
					<div class="font">
						<h3><span>{{ i.Title }}</span></h3>
						<p>{{ i.Describe }}</p>
					</div>
				</a></li> -->
      </ul>
      <pageMove @pageindex="pageF" :pageOption="pageOption"></pageMove>
    </div>
    <div class="pageHotZy">
      <hotzy></hotzy>
    </div>
  </div>
</template>

<script>
export default {
  name: "datefontlist",
  data() {
    return {
      pathList: [],
      pageList: [],
      pageOption: {
        pageIndex: 1,
        pageTotal: 0,
        pageSize: 7,
      },
    };
  },
  created() {
    this.pathList = this.$route.query;
    this.getPageList();
    // console.log(this.pageList)
  },
  methods: {
    pageF(data) {
      this.pageOption.pageIndex = data;
      this.getPageList();
    },
    getPageList() {
      var temp = this.Base.GetNewsInfoByCid(
        "",
        this.pathList.cid,
        "",
        "",
        "",
        this.pageOption.pageIndex,
        "",
        this.pageOption.pageSize
      );
      this.pageList = temp.row;
      this.pageOption.pageTotal = temp.total;
    },
  },
};
</script>

<style scoped>
.pageCont {
  float: left;
  width: 900px;
}
.pageHotZy {
  float: right;
  width: 260px;
  box-shadow: 0px 0px 16px 0px rgba(43, 60, 198, 0.2);
}
.pageHotZy dd {
  width: 100%;
  height: 122px;
  position: relative;
}
.pageHotZy img {
  display: block;
  width: 100%;
  height: 100%;
}
.pageHotZy span {
  width: 100%;
  height: 37px;
  line-height: 37px;
  color: #f7f7fa;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute;
  left: 0px;
  bottom: 0px;
}
.pageHotZy dt {
  width: 100%;
  height: 91px;
}
ul {
  padding-bottom: 100px;
}
li {
  height: 112px;
  background-color: #f3f3f3;
  margin-bottom: 29px;
  transition: box-shadow 0.4s;
}
li:last-child {
  margin-bottom: 0px;
}
li .date {
  width: 120px;
  height: 112px;
  float: left;
  background-color: #4273ff;
}
li .date b {
  display: block;
  text-align: center;
  width: 95px;
  margin: 0 auto;
  font-size: 42px;
  color: white;
  font-weight: normal;
  line-height: 32px;
  padding-top: 20px;
  padding-bottom: 12px;
  border-bottom: dashed 1px white;
}
li .date span {
  display: block;
  text-align: center;
  margin-top: 11px;
  line-height: 1;
  font-size: 22px;
  color: white;
}
li .font {
  width: 736px;
  float: right;
  margin-right: 23px;
}
li .font h3 {
  height: 24px;
  line-height: 24px;
  margin-top: 15px;
}
li .font h3 span {
  display: block;
  font-size: 18px;
  color: #333333;
  height: 24px;
  transition: all 0.4s;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  max-width: 500px;
  float: left;
}
li .font h3 i {
  display: block;
  float: right;
  color: #666666;
  position: relative;
}
li .font h3 i::before {
  content: "";
  width: 16px;
  height: 16px;
  background: url(../assets/img/time_icon.png) center no-repeat;
  position: absolute;
  top: 4px;
  left: -20px;
}
li .font p {
  height: 52px;
  text-align: justify;
  margin-top: 12px;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
  color: #999999;
  line-height: 26px;
}
li:hover {
  box-shadow: 0px 0px 16px 0px rgba(43, 60, 198, 0.2);
}
li:hover .font h3 span {
  color: #4273ff;
}
</style>



