<template>
	<div class="search">
		<div class="pageBanner"></div>
		<div class="pageNav">
			<h3><span>搜索结果</span></h3>
		</div>
		<div class="pageMain clearfix">
			<div class="datefontlist">
				<div class="pageCont">
					<ul>
						<template v-if="searchList.length>0">
							<li v-for="i in searchList"><router-link :to="{path:'/list/content',query:{id:i.ID}}">
								<div class="date"><b>{{ Curren.formatDatebox(i.ShowTime,"",2) }}</b><span>{{ Curren.formatDatebox(i.ShowTime,"-",6) }}</span></div>
								<div class="font">
									<h3><span v-html="i.Title"></span></h3>
									<p v-html="i.Describe"></p>
								</div>
							</router-link></li>
						</template>
						<template v-else>
							<div style="width: 100%;height: 100%;text-align: center;font-size: 14px;color: #999999;">未搜索到有关信息</div>
						</template>
					</ul>
					<pageMove @pageindex="pageF" :pageOption="pageOption"></pageMove>
				</div>
				<div class="pageHotZy">
					<hotzy></hotzy>
				</div>
			</div>	
		</div>
	</div>
</template>

<script>
export default {
	name: 'search',
	data() {
		return {
			searchList: [],
			searchWords: '',
			pathList: [],
			pageList: [],
			pageOption: {
				pageIndex: 1,
				pageTotal: 0,
				pageSize: 7
			}
		};
	},
	created() {
		this.searchWords = this.$route.query.searchwords;
		// this.searchList = this.Base.GetInfoBySearch(this.searchWords, '', '', '', 1, 6).row;
		this.getPageList()
		// console.log(this.searchList);
	},
	methods: {
		pageF(data) {
			this.pageOption.pageIndex = data;
			this.getPageList();
		},
		getPageList() {
			var temp = this.Base.GetInfoBySearch(this.searchWords, '', '', '', this.pageOption.pageIndex, this.pageOption.pageSize);
			// console.log(temp);
			this.pageOption.pageTotal = temp.total;
			this.searchList = temp.row;
		}
	}
};
</script>

<style scoped>
.list{width: 100%;overflow: hidden;padding-bottom: 40px;}
.pageBanner{width: 100%;height: 300px;background: url(../assets/img/pageBanner.jpg) center no-repeat;}
.pageNav{width: 1200px;height: 50px;background-color: #f7f7fa;margin: 0 auto;}
.pageNav h3{float: left;width: 260px;height: 50px;line-height: 50px;text-indent: 82px;font-size: 24px;color: white;position: relative;}
.pageNav h3::before{content: "";width: 220px;height: 70px;bottom: 0px;left: 0px;position: absolute;background-color: #4273ff;}
.pageNav h3::after{content: "";width: 0px;height: 0px;bottom: 0px;right: 0px;position: absolute;border-left: solid 20px #4273FF;border-bottom: solid 35px #4273FF;border-right: solid 20px transparent;border-top: solid 35px transparent; }
.pageNav h3 span{display: block;width: 100%;height: 100%;position: absolute;left: 0px;top: 0px;z-index: 3;}
.pageNav ul{float: right;width: 900px;display: flex;}
.pageNav ul li{flex: 1;height: 50px;display: flex;align-items: center;justify-content: center;}
.pageNav ul li a{font-size: 18px;color: #333333;text-align: center;line-height: 1.2;}
.pageNav ul li a.router-link-active,.pageNav ul li:hover a{color: #4273ff;}
.pageMain{width: 1200px;margin: 0 auto;margin-top: 50px;padding-top: 1px;padding-bottom: 40px;}
.pageCont{float: left;width: 900px;}
.pageHotZy{float: right;width: 260px;box-shadow: 0px 0px 16px 0px rgba(43, 60, 198, 0.2);}
.pageHotZy dd{width: 100%;height: 122px;position: relative;}
.pageHotZy img{display: block;width: 100%;height: 100%;}
.pageHotZy span{width: 100%;height: 37px;line-height: 37px;color: #f7f7fa; text-align: center;background-color: rgba(0,0,0,0.5);position: absolute;left: 0px;bottom: 0px;}
.pageHotZy dt{width: 100%;height: 91px;}
.pageCont ul{padding-bottom: 100px;}
.pageCont li{height: 112px;background-color: #f3f3f3;margin-bottom: 29px;transition: box-shadow 0.4s;}
.pageCont li:last-child{margin-bottom: 0px;}
.pageCont li .date{width: 120px;height: 112px;float: left;background-color: #4273ff}
.pageCont li .date b{display: block;text-align: center;width: 95px;margin: 0 auto;font-size: 42px;color: white;font-weight: normal;line-height: 32px;padding-top: 20px;padding-bottom: 12px;border-bottom: dashed 1px white;}
.pageCont li .date span{display: block;text-align: center;margin-top: 11px;line-height: 1;font-size: 22px;color: white;}
.pageCont li .font{width: 736px;float: right;margin-right: 23px;}
.pageCont li .font h3{height: 24px;line-height: 24px;margin-top: 15px;}
.pageCont li .font h3 span{display: block;font-size: 18px;color: #333333;height: 24px;transition: all 0.4s; overflow: hidden;text-overflow: ellipsis;white-space: nowrap;max-width: 500px; float: left;}
.pageCont li .font h3 i{display: block;float: right;color: #666666;position: relative;}
.pageCont li .font h3 i::before{content: "";width: 16px;height: 16px;background: url(../assets/img/time_icon.png) center no-repeat;position: absolute;top: 4px;left: -20px;}
.pageCont li .font p{height: 52px;text-align: justify;margin-top: 12px; display: -webkit-box; -webkit-box-orient: vertical; -webkit-line-clamp: 2; overflow: hidden;color: #999999;line-height: 26px;}
.pageCont li:hover{box-shadow: 0px 0px 16px 0px rgba(43, 60, 198, 0.2);}
.pageCont li:hover .font h3 span{color: #4273ff;}
</style>



