<template>
  <div class="yzyx">
    <div class="pageCont">
      <div class="pageYzxx">
        <div class="pageYzxxCont">
          <p style="text-indent: 0em;">尊敬的来信人，您好！</p>
          <p>为进一步增进学校领导与师生员工的密切联系，更好地解决群众普遍关心的实际问题，学院专门设立了"校长信箱"（以下简称"信箱"）。为了使"信箱"更好地发挥作用，请您在使用时注意以下几点：</p>
          <p>1、学院欢迎广大师生员工就五月花发展建设中存在的问题、各单位工作人员的职务行为等提出意见和建议。</p>
          <p>2、来信内容应遵守中华人民共和国法律、法规，并应客观真实。</p>
          <p>3、建议您参照校领导分工，根据所反映问题将信件投送给相应领导，以便信件得到更有针对性的处理。</p>
          <p>4、建议您尽量详细地描述所反映问题，并填写真实姓名和联系方式，以便及时反馈办理情况。</p>
          <p>5、对于已经答复过的事项，"信箱"可不予受理，请勿就答复过的问题重复提交信件。</p>
          <p>感谢您对我们工作的支持与配合！</p>
          <div class="yzxxBtn">
            <h3>院长信箱</h3>
            <div class="openFrom">给他写信</div>
          </div>
        </div>
        <div class="pageYzxxSendBg">
          <div class="fontBox pageYzxxSend">
            <div class="page_bgyxCont clearfix">
              <h3 class="Title">院长信箱</h3>
              <div class="page_bgyxContBg">
                <table border="0" cellspacing="0" cellpadding="0">
                  <tr>
                    <td class="width1">
                      <span>姓名*</span>
                      <input type="text" placeholder="请输入您的姓名" v-model="Senddata.Name" />
                    </td>
                    <td class="width2" width="428">
                      <span>邮箱*</span>
                      <input
                        type="text"
                        required="required"
                        placeholder="请输入您的电子邮箱"
                        v-model="Senddata.Email"
                      />
                    </td>
                  </tr>
                  <tr>
                    <td class="width1" colspan="2">
                      <span>电话*</span>
                      <input type="text" placeholder="请输入您的电话号码" v-model="Senddata.Mobile" />
                    </td>
                  </tr>
                  <tr>
                    <td class="width3" colspan="2">
                      <span>标题*</span>
                      <input type="text" placeholder="请输入标题" v-model="Senddata.Title" />
                    </td>
                  </tr>
                  <tr>
                    <td class="width3 contant" colspan="2">
                      <span>内容*</span>
                      <textarea rows cols placeholder="请输入内容" v-model="Senddata.Info"></textarea>
                    </td>
                  </tr>
                </table>
                <div class="sendBtn" @click="sendMailF()">点击发送</div>
                <div class="closeBtn el-icon-close"></div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="pageHotZy">
      <hotzy></hotzy>
    </div>
  </div>
</template>

<script>
export default {
  name: "yzyx",
  data() {
    return {
      dialogImageUrl: "",
      dialogVisible: false,
      disabled: false,
      Senddata: {
        Name: "",
        Email: "",
        Mobile: "",
        Title: "",
        Info: "",
        ColumnID: this.api.bgyx
      }
    };
  },
  mounted() {
    $(".yzxxBtn .openFrom").click(function() {
      $(".pageYzxxSendBg").fadeIn();
    });
    $(".pageYzxxSend .closeBtn").click(function() {
      $(".pageYzxxSendBg").fadeOut();
    });
  },
  methods: {
    sendMailF: function() {
      var that = this;
      var data = that.Senddata;
      for (var a in data) {
        if (that.Curren.isEmpty(data[a])) {
          alert("请输入完整的信息！");
          return false;
        }
      }
      if (!/^1[3456789]\d{9}$/.test(data.Mobile)) {
        alert("手机号码有误，请重填");
        return false;
      }
      var result = this.Base.sendMail(data);
      alert(result.message);
    }
  }
};
</script>

<style scoped>
.pageCont {
  float: left;
  width: 900px;
}
.pageHotZy {
  float: right;
  width: 260px;
  box-shadow: 0px 0px 16px 0px rgba(43, 60, 198, 0.2);
}
.pageHotZy dd {
  width: 100%;
  height: 122px;
  position: relative;
}
.pageHotZy img {
  display: block;
  width: 100%;
  height: 100%;
}
.pageHotZy span {
  width: 100%;
  height: 37px;
  line-height: 37px;
  color: #f7f7fa;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute;
  left: 0px;
  bottom: 0px;
}
.pageHotZy dt {
  width: 100%;
  height: 91px;
}
.pageYzxx {
  width: 100%;
  margin: 0 auto;
  background-color: white;
  padding-bottom: 57px;
  letter-spacing: 0;
  box-shadow: 0px 0px 16px 0px rgba(43, 60, 198, 0.2);
}
.pageYzxx .pageYzxxCont {
  width: 835px;
  margin: 0 auto;
  padding-top: 40px;
}
.pageYzxx .pageYzxxCont p {
  text-indent: 2em;
  font-size: 15px;
  color: #333333;
  line-height: 30px;
  text-align: justify;
  margin-bottom: 30px;
}
.yzxxBtn {
  width: 390px;
  height: 150px;
  margin: 0 auto;
  text-align: center;
  margin-top: 55px;
  background: url(../assets/img/yzyx_bg.png) center no-repeat;
  overflow: hidden;
}
.yzxxBtn h3 {
  font-size: 24px;
  color: #333333;
  display: block;
  line-height: 1;
  text-align: left;
  text-indent: 145px;
  margin-top: 42px;
  position: relative;
  letter-spacing: 2px;
}
.yzxxBtn .openFrom {
  display: block;
  margin-left: 140px;
  width: 118px;
  height: 34px;
  text-align: center;
  line-height: 34px;
  border: solid 1px #4273ff;
  color: #4273ff;
  letter-spacing: 4px;
  border-radius: 17px;
  margin-top: 17px;
  cursor: pointer;
}
.pageYzxxSendBg {
  display: none;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
}
.pageYzxxSendBg * {
  letter-spacing: 0;
}
.pageYzxxSend {
  width: 1000px;
  height: 755px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -500px;
  margin-top: -375px;
  background-color: white;
  box-shadow: 0px 0px 5px 0px rgba(6, 0, 1, 0.3);
  border-radius: 10px;
}
.pageYzxxSend .closeBtn {
  width: 30px;
  height: 30px;
  font-size: 30px;
  text-align: center;
  line-height: 30px;
  position: absolute;
  right: 5px;
  top: 5px;
  color: #4a77f6;
  margin: 0;
  border-radius: 15px;
  background-color: rgba(0, 0, 0, 0.1);
  cursor: pointer;
}
.fontBox .Title {
  text-align: center;
  font-size: 20px;
  color: #333333;
  line-height: 24px;
  margin-bottom: 30px;
  font-weight: bold;
  letter-spacing: 2px;
}
.fontBox .FTitle {
  line-height: 15px;
  text-align: center;
  margin-bottom: 24px;
}
.fontBox .FTitle span {
  margin: 0px 13px;
  font-size: 16px;
  color: #666666;
}
.page_bgyxCont {
  background-color: white;
  box-shadow: 0 0 22px rgba(0, 0, 0, 0.2);
  padding-top: 30px;
  border-radius: 15px;
}
.page_bgyxCont .page_bgyxContBg {
  background-size: 100% auto;
  padding-bottom: 30px;
  padding-top: 1px;
}
.moneyBox input::-webkit-input-placeholder {
  /* WebKit browsers */
  color: #999999;
  font-size: 14px;
}
.moneyBox input::-moz-placeholder {
  /* WebKit browsers */
  color: #999999;
  font-size: 14px;
}
.moneyBox input::-ms-input-placeholder {
  /* WebKit browsers */
  color: #999999;
}
.page_bgyxCont table {
  width: 815px;
  max-width: 815px;
  margin: 0 auto;
}
.page_bgyxCont table span {
  display: inline-block;
  margin-right: 20px;
  font-size: 16px;
  color: #4a77f6;
}
.page_bgyxCont table td {
  padding-bottom: 29px;
}
.page_bgyxCont table td input {
  border: solid 1px #4a77f6;
  text-indent: 10px;
  font-size: 14px;
}
.page_bgyxCont table td.width1 input {
  width: 275px;
}
.page_bgyxCont table td.width2 input {
  width: 362px;
}
.page_bgyxCont table td.width3 input,
.page_bgyxCont table td.width3 textarea {
  width: 750px;
  max-width: 815px;
}
.page_bgyxCont table td.width3 textarea {
  min-height: 300px;
  box-sizing: border-box;
  border: solid 1px #4a77f6;
  padding: 0 10px;
  font-size: 14px;
}
.page_bgyxCont table .contant span {
  display: block;
  float: left;
}
.page_bgyxCont .sendBtn {
  display: block;
  width: 200px;
  height: 30px;
  margin: 0 auto;
  text-align: center;
  font-size: 16px;
  color: #4a77f6;
  line-height: 28px;
  border-radius: 15px;
  border: solid 1px #4a77f6;
  box-sizing: border-box;
}
.page_bgyxCont .sendBtn:hover {
  background-color: #4a77f6;
  color: white;
}
.page_bgyxCont .page_bgyxContBg {
  background: none;
}
.page_bgyxCont table,
.page_bgyxCont tbody {
  width: 750px;
  display: block;
}
.page_bgyxCont table td {
  display: block;
  overflow: hidden;
  width: 750px !important;
  padding: 0 25px;
  padding-bottom: 30px;
  box-sizing: border-box;
}
.page_bgyxCont table td.width2 input {
  width: 275px;
}
.page_bgyxCont table td.width3 input,
.page_bgyxCont table td.width3 textarea {
  width: 620px;
  float: left;
  font-size: 14px;
}
.page_bgyxCont table span {
  display: block;
  float: left;
  width: 75px;
  height: 36px;
  line-height: 36px;
  margin-right: 0;
  font-size: 16px;
  color: #4a77f6;
}
.page_bgyxCont table td input {
  display: block;
  float: left;
  height: 36px;
  box-sizing: border-box;
  font-size: 14px;
}
.page_bgyxCont .sendBtn {
  width: 230px;
  height: 46px;
  line-height: 46px;
  border-radius: 23px;
  font-size: 18px;
  color: white;
  background-color: #4a77f6;
  cursor: pointer;
}
</style>



