<template>
  <div class="zysz">
    <ul class="clearfix">
      <li v-for="i in zyszList" :key="i.ID">
        <router-link :to="i.Url">
          <div class="imgBox">
            <img :src="i.Cover" />
          </div>
        </router-link>
        <div class="font">
          <router-link :to="{ path: i.Url }">
            <h3>{{ i.Title }}</h3>
            <p>
              <span>招生对象</span>
              —{{ i.Author }}
            </p>
            <p>
              <span>就业方向</span>
              —{{ i.Source }}
            </p>
          </router-link>
          <dl>
            <dd class="fl">
              <span @click="openzxbm">在线报名</span>
            </dd>
            <dd class="fr">
              <a :href="bdsq" target="_blank">专业咨询</a>
            </dd>
          </dl>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "zysz",
  data() {
    return {
      zyszList: [],
    };
  },
  mounted() {
    var that = this;
    // var navList = that.Navformat(that.Base.GetNavColumnsByDid());
    // var tempList = [];
    // for(let key  in navList){
    // 	if(navList[key].ID == that.api.zysz){
    // 		tempList = navList[key].Clist
    // 	}
    //  }
    //  for(let key in tempList){
    // 	 tempList[key].jdzg=[];
    // 	 tempList[key].jdzg = that.Base.GetNewsInfoByCid('',tempList[key].ID).row[0]
    // 	 // console.log(tempList[key])
    //  }
    //  console.log(tempList)
    
    that.zyszList = that.Base.GetNewsInfoByCid(
      "",
      that.api.zysz,
      // Number(that.$route.query.cid),
      "",
      "",
      "",
      1,
      "",
      30
    ).row;
    // console.log(that.zyszList,"that.zyszList")
  },
  methods: {
    Navformat: function (data) {
      var obj = data;

      var temp = [];
      for (var i = 0; i < obj.length; i++) {
        if (obj[i].FID == "0") {
          var main = obj[i];
          main.Clist = [];
          for (var j = 0; j < obj.length; j++) {
            if (obj[j].FID == main.ID) {
              main.Clist.push(obj[j]);
            }
          }
          temp.push(main);
        }
      }
      return temp;
    },
    openzxbm: function () {
      this.$store.dispatch("showAddStudent");
    },
  },
};
</script>

<style scoped>
ul {
  padding-bottom: 35px;
  padding-bottom: 15px;
  font-size: 0;
}
li {
  width: 590px;
  height: 177px;
  float: left;
  position: relative;
  margin-bottom: 40px;
  box-shadow: 0px 13px 18px 0px rgba(31, 59, 173, 0.18);
}
li:nth-child(even) {
  float: right;
}
li .imgBox {
  width: 232px;
  height: 177px;
  overflow: hidden;
  float: left;
  position: relative;
}
li .imgBox::after {
  content: "";
  width: 0px;
  height: 0px;
  position: absolute;
  bottom: 0px;
  right: 0px;
  border-right: solid 15px white;
  border-left: solid 15px transparent;
  border-top: solid 88.5px transparent;
  border-bottom: solid 88.5px white;
}
li .imgBox img {
  transition: all 0.4s;
}
li .font {
  width: 358px;
  height: 115px;
  box-sizing: border-box;
  padding-left: 10px;
  float: right;
  font-size: 16px;
  background-color: white;
  transition: all 0.4s;
}
li .font h3 {
  height: 30px;
  line-height: 30px;
  text-indent: 14px;
  border-left: solid 6px #4a77f6;
  font-size: 24px;
  color: #4a77f6;
  letter-spacing: 2px;
  margin-top: 10px;
  margin-bottom: 15px;
}
li .font p {
  line-height: 30px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
li .font p span {
  color: #f2a354;
}
li .font dl {
  margin-top: 14px;
}
li .font dl dd a,
li .font dl dd span {
  font-size: 15px;
  color: white;
  display: block;
  width: 100%;
  height: 100%;
  line-height: 38px;
  text-align: center;
}
li .font dl dd.fl {
  width: 130px;
  height: 38px;
  background-image: linear-gradient(90deg, #88d4fb 1%, #62a4f8 100%);
  border-radius: 19px;
  cursor: pointer;
}
li .font dl dd.fr {
  margin-left: 20px;
  float: left;
  width: 130px;
  height: 38px;
  background-image: linear-gradient(-90deg, #de803a 0%, #eca55f 100%);
  border-radius: 19px;
}
</style>



