<template>
  <div class="dxzn">
    <div class="pageCont">
      <p>
        <i class="icon1"></i>
        <b>学校地址</b>
        <span>{{ bmList.Address }}</span>
      </p>
      <p>
        <i class="icon2"></i>
        <b>联系方式</b>
        <span>{{ bmList.Phone }}</span>
      </p>
      <p>
        <i class="icon3"></i>
        <b>乘车路线</b>
        <span>火车北站：地铁7号线→地铁5号线→690路公交车直达</span>
        <span>地铁7号线→101路公交车→712路公交车</span>
        <span>成都东站：地铁7号线→地铁5号线→690路公交车直达</span>
        <span>地铁2号线→地铁5号线→690路公交车直达</span>
        <span>石羊场客运站：100路公交车→地铁5号线→690路公交车直达</span>
        <span>K16路/61路→K13路公交车→712路公交车</span>
        <span>茶店子客运站：723路公交车→P08路/P18路/定时公交P51路公交车</span>
        <span>710路公交车→P08路/P18路/定时公交P51路公交车</span>
        <span>五块石客运站：103路公交车→地铁5号线→690路公交车直达</span>
        <span>316路/24路公交车→690路公交车直达</span>
      </p>
      <div class="mapBox">
        <baidu-map
          class="towerMap"
          :center="center"
          :zoom="zoom"
          @ready="handler"
          ak="v6lDhuHF9LcLReGAFxqWVYC2CwvkVdQe"
        >
          <bm-navigation anchor="BMAP_ANCHOR_TOP_RIGHT"></bm-navigation>
          <bm-marker :position="center" :dragging="false" animation="BMAP_ANIMATION_BOUNCE"></bm-marker>
          <bm-geolocation
            anchor="BMAP_ANCHOR_BOTTOM_RIGHT"
            :showAddressBar="true"
            :autoLocation="true"
          ></bm-geolocation>
        </baidu-map>
      </div>
    </div>
    <div class="pageHotZy">
      <hotzy></hotzy>
    </div>
  </div>
</template>

<script>
export default {
  name: "dxzn",
  data() {
    return {
      center: { lng: 0, lat: 0 },
      zoom: 3,
      bmList: [],
      ccroud: []
    };
  },
  mounted() {
    this.bmList = this.Base.GetDepartmentByDid();
    this.ccroud = this.Base.GetColumnByid(this.api.dxzn);
  },
  methods: {
    handler({ BMap, map }) {
      // console.log(BMap, map)
      (this.center.lng = 103.984242), (this.center.lat = 30.830813);
      this.zoom = 18;
    }
  }
};
</script>

<style scoped>
.pageCont {
  float: left;
  width: 900px;
}
.pageHotZy {
  float: right;
  width: 260px;
  box-shadow: 0px 0px 16px 0px rgba(43, 60, 198, 0.2);
}
.pageHotZy dd {
  width: 100%;
  height: 122px;
  position: relative;
}
.pageHotZy img {
  display: block;
  width: 100%;
  height: 100%;
}
.pageHotZy span {
  width: 100%;
  height: 37px;
  line-height: 37px;
  color: #f7f7fa;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute;
  left: 0px;
  bottom: 0px;
}
.pageHotZy dt {
  width: 100%;
  height: 91px;
}
.pageCont {
  box-shadow: 0px 0px 16px 0px rgba(43, 60, 198, 0.2);
  padding-top: 30px;
  padding-bottom: 37px;
}
.pageCont p {
  padding: 0px 20px;
  overflow: hidden;
  line-height: 39px;
  margin-bottom: 50px;
}
.pageCont p i {
  display: block;
  width: 34px;
  height: 39px;
  float: left;
  margin-right: 10px;
}
.pageCont p .icon1 {
  background: url(../assets/img/dxzn_icon1.png) center no-repeat;
}
.pageCont p .icon2 {
  background: url(../assets/img/dxzn_icon2.png) center no-repeat;
}
.pageCont p .icon3 {
  background: url(../assets/img/dxzn_icon3.png) center no-repeat;
}
.pageCont p b {
  display: block;
  float: left;
  height: 26px;
  line-height: 23px;
  margin-top: 7px;
  border-right: solid 3px #333333;
  font-size: 24px;
  font-weight: normal;
  padding-right: 8px;
}
.pageCont p span {
  float: right;
  display: block;
  width: 700px;
  text-align: justify;
  font-size: 18px;
}
.pageCont p:last-child span {
  line-height: 30px;
}
.mapBox {
  width: 715px;
  height: 463px;
  border-radius: 10px;
  margin: 0 auto;
  overflow: hidden;
}
.mapBox .towerMap {
  width: 100%;
  height: 100%;
}
</style>



