<template>
  <div class="pagecontent">
    <div class="pageCont">
      <div class="content-cont">
        <div class="content-cont-title">{{ pageList.Title }}</div>
        <div class="content-cont-ftitle">
          <span
            ><i class="el-icon-time"></i><em>{{ pageList.ShowTime }}</em></span
          ><span
            ><i class="el-icon-view"></i><em>{{ pageList.Press }}</em></span
          >
        </div>
        <!-- {{pageList.Details}} -->
        <div class="content-cont-html" v-html="pageList.Details"></div>
      </div>
    </div>
    <div class="pageHotZy">
      <hotzy></hotzy>
    </div>
  </div>
</template>

<script>
export default {
  name: "pagecontent",
  data() {
    return {
      routerList: [],
      pageList: [],
    };
  },
  mounted() {
    var that = this;
    that.routerList = that.$route.query;

    that.pageList = that.Base.GetNewsInfoByNid(that.routerList.id);
    
  },
  methods: {},
};
</script>

<style scoped>
.pageCont {
  float: left;
  width: 900px;
}
.pageHotZy {
  float: right;
  width: 260px;
  box-shadow: 0px 0px 16px 0px rgba(43, 60, 198, 0.2);
}
.pageHotZy dd {
  width: 100%;
  height: 122px;
  position: relative;
}
.pageHotZy img {
  display: block;
  width: 100%;
  height: 100%;
}
.pageHotZy span {
  width: 100%;
  height: 37px;
  line-height: 37px;
  color: #f7f7fa;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute;
  left: 0px;
  bottom: 0px;
}
.pageHotZy dt {
  width: 100%;
  height: 91px;
}
.content-cont {
  width: 100%;
  min-height: 824px;
  padding: 30px 30px;
  box-sizing: border-box;
  background-color: #ffffff;
  box-shadow: 0px 0px 16px 0px rgba(43, 60, 198, 0.2);
}
.content-cont .content-cont-title {
  text-align: center;
  font-size: 20px;
  line-height: 30px;
  font-weight: bold;
  padding-bottom: 15px;
  border-bottom: solid 1px #eeeeee;
}
.content-cont-ftitle {
  text-align: center;
  font-size: 14px;
  color: #666666;
  line-height: 18px;
  height: 18px;
  margin-top: 20px;
  margin-bottom: 20px;
}
.content-cont-ftitle span {
  margin: 0px 15px;
}
.content-cont-ftitle span i {
  margin-right: 5px;
}
.content-cont-html {
  text-align: justify;
  text-indent: 2em;
  line-height: 30px;
}
.content-cont-html >>> img {
  display: block;
  margin: 0 auto;
  max-width: 100%;
}
</style>



