<template>
  <div class="list">
    <div
      class="pageBanner"
      :style="{background: 'url('+pageBanner+') center no-repeat'}"
    >
      
    </div>
    <div class="pageNav">
      <h3>
        <span>{{ pageNavList.Name }}</span>
      </h3>
      <ul v-if="pageNavList.Clist">
        <li v-for="i in pageNavList.Clist" :key="i.ID">
          <hrefformat
            :url="i.Url"
            :param="{ cid: i.ID }"
            :content="i.Name"
          ></hrefformat>
        </li>
      </ul>
    </div>
    <div class="pageMain clearfix">
      <div class="hdsg">
        <ul class="clearfix">
          <li v-for="i in pageList" @click="openVideo(i)" :key="i.ID">
            <div class="imgBox">
              <img :src="i.FileUrl" alt />
            </div>
            <p>{{ i.Title }}</p>
          </li>
        </ul>
        <pageMove @pageindex="pageF" :pageOption="pageOption"></pageMove>
        <div id="videoPaly" v-if="videoFlag" @click="videoFlag = false">
          <div class="video_flag_main">
            <div class="video_flag_close" @click="videoFlag = false"></div>
            <div class="video_flag_box">
              <iframe
                width="100%"
                height="100%"
                :src="xcpPalyActive.Url"
                frameborder="0"
              ></iframe>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "hdsg",
  data() {
    return {
      pageCid: "",
      pageFCid: "",
      pageNavList: [],
      pageRouterList: [],
      pageBanner: require("../assets/img/pageBanner.jpg"),
      pathList: [],
      pageList: [],
      pageOption: {
        pageIndex: 1,
        pageTotal: "",
        pageSize: 9,
      },
      xcpPalyActive: [],
      videoFlag: false,
    };
  },
  created() {
    this.pathList = this.$route.query;

    this.getPageList();
  },
  mounted() {
    var that = this;
    that.pageRouterList = that.$route.query;
    if (that.pageRouterList.cid) {
      that.pageCid = that.pageRouterList.cid;
      that.navFindF();
    } else {
      that.pageCid = that.Base.GetNewsInfoByNid(that.pageRouterList.id).Fid;
      that.navFindF();
    }
    if (!(that.pageNavList.Logo == "http://base.hope55.com"))
      that.pageBanner = that.pageNavList.Logo;

    
  },
  methods: {
    Navformat: function (data) {
      var obj = data;
      // console.log(obj);
      var temp = [];
      for (var i = 0; i < obj.length; i++) {
        if (obj[i].FID == "0") {
          var main = obj[i];
          main.Clist = [];
          for (var j = 0; j < obj.length; j++) {
            if (obj[j].FID == main.ID) {
              main.Clist.push(obj[j]);
            }
          }
          temp.push(main);
        }
      }
      return temp;
    },
    navFindF: function (id) {
      var that = this;
      var navList = that.Navformat(that.Base.GetNavColumnsByDid());
      if (that.Base.GetColumnByid(that.pageCid).Fid) {
        that.pageFCid = that.Base.GetColumnByid(that.pageCid).Fid;
        for (let key in navList) {
          if (navList[key].ID == that.pageFCid) {
            that.pageNavList = navList[key];
          }
        }
      } else {
        for (let key in navList) {
          if (navList[key].ID == that.pageCid) {
            that.pageNavList = navList[key];
          }
        }
      }
    },
    pageF(data) {
      this.pageOption.pageIndex = data;
      this.getPageList();
    },
    getPageList() {
      var temp = this.Base.GetFilesInfoByCid(
        "",
        this.pathList.cid,
        "",
        "",
        this.pageOption.pageIndex,
        "",
        this.pageOption.pageSize
      );
      this.pageList = temp.GetData;
      this.pageOption.pageTotal = temp.Total;
      // console.log(temp);
    },
    openVideo: function (obj) {
      var that = this;
      that.videoFlag = true;
      that.xcpPalyActive = obj;
    },
  },
};
</script>

<style scoped>
.list {
  width: 100%;
  overflow: hidden;
  padding-bottom: 40px;
}
.pageBanner {
  width: 100%;
  height: 400px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.pageNav {
  width: 1200px;
  height: 50px;
  background-color: #f7f7fa;
  margin: 0 auto;
  margin-top: 40px;
}
.pageNav h3 {
  float: left;
  width: 260px;
  height: 50px;
  line-height: 50px;
  text-indent: 82px;
  font-size: 24px;
  color: white;
  position: relative;
}
.pageNav h3::before {
  content: "";
  width: 220px;
  height: 70px;
  bottom: 0px;
  left: 0px;
  position: absolute;
  background-color: #4273ff;
}
.pageNav h3::after {
  content: "";
  width: 0px;
  height: 0px;
  bottom: 0px;
  right: 0px;
  position: absolute;
  border-left: solid 20px #4273ff;
  border-bottom: solid 35px #4273ff;
  border-right: solid 20px transparent;
  border-top: solid 35px transparent;
}
.pageNav h3 span {
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0px;
  top: 0px;
  z-index: 3;
}
.pageNav ul {
  float: right;
  width: 900px;
  display: flex;
}
.pageNav ul li {
  flex: 1;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.pageNav ul li a {
  font-size: 18px;
  color: #333333;
  text-align: center;
  line-height: 1.2;
}
.pageNav ul li a.router-link-active,
.pageNav ul li:hover a {
  color: #4273ff;
}
.pageMain {
  width: 1200px;
  margin: 0 auto;
  margin-top: 50px;
  padding-top: 1px;
}
.hdsg ul {
  width: 1230px;
  padding-top: 10px;
  margin-bottom: 40px;
}
.hdsg li {
  float: left;
  width: 380px;
  margin-right: 30px;
  margin-top: 50px;
  cursor: pointer;
}
.hdsg li .imgBox {
  width: 380px;
  height: 223px;
  border-radius: 10px;
  position: relative;
  overflow: hidden;
  transition: all 0.4s;
}
.hdsg li img {
  display: block;
  width: 380px;
  height: 223px;
  border-radius: 10px;
}
.hdsg li .imgBox::before {
  content: "";
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  position: absolute;
  top: 0;
  left: 0;
  pointer-events: none;
}
.hdsg li .imgBox::after {
  content: "";
  width: 63px;
  height: 63px;
  background: url(../assets/img/videoPlayBtn.png) center no-repeat;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
}
.hdsg li p {
  max-width: 95%;
  margin: 0 auto;
  text-align: center;
  font-size: 24px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  line-height: 1.2;
  margin-top: 20px;
}
.hdsg li:hover .imgBox {
  transform: translateY(-5px);
}
</style>



