        prevButton: ".sxsb .swiper-button-prev",
        loop: false,
      },
      swiperOption3: {
        //设置点击箭头
        nextButton: ".xysh .swiper-button-next",
        prevButton: ".xysh .swiper-button-prev",
        loop: false,
      },
      ksgzList: [],
      zyszList: [],
      bmjyList: [],
      syanList: {
        btn1: [],
        btn2: [],
        btn3: [],
        btn4: [],
      },
      xyywBigImg: "",
      xyywList: [],
      tzggList: [],
      mtgzList: [],
      mlxyShow: 0,
      mlxyList: {
        xyfgList: [],
        sxsbList: [],
        xyshList: [],
      },
      xcpList: [],
      xcpPalyActive: [],
      videoFlag: false,
      Six_major_advantages: [],
    };
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.$swiper;
    },
    classOption: function () {
      return {
        step: 0.5,
        limitMoveNum: 5,
      };
    },
    classOption1: function () {
      return {
        step: 1,
        limitMoveNum: 5,
        singleHeight: 30,
        waitTime: 2500,
      };
    },
  },
  mounted() {
    var that = this;
    that.bmjyList = that.Base.GetStuCircleList().data;
    that.bannerList = that.Base.GetFilesInfoByCid("", that.api.banner).GetData;
    that.ksgzList = that.Base.GetNewsInfoByCid("", that.api.ksgz).row;
    that.xyywList = that.Base.GetNewsInfoByCid(
      "",
      that.api.xyyw,
      "",
      "",
      "",
      1,
      "",
      5
    ).row;
    that.xyywBigImg = that.xyywList[0].Cover;
    that.tzggList = that.Base.GetNewsInfoByCid(
      "",
      that.api.tzgg,
      "",
      "",
      "",
      1,
      "",
      5
    ).row;
    that.mtgzList = that.Base.GetNewsInfoByCid(
      "",
      that.api.mtgz,
      "",
      "",
      "",
      1,
      "",
      5
    ).row;
    that.mlxyList.xyfgList = that.listFormat(that.api.xyfg, 5);
    that.mlxyList.sxsbList = that.listFormat(that.api.sxsb, 5);
    that.mlxyList.xyshList = that.listFormat(that.api.xysh, 5);

    // console.log(that.mlxyList.xyfgList, "that.mlxyList.xyfgList");

    that.Six_major_advantages = that.Base.GetNewsInfoByCid(
      "",
      that.api.ldys,
      "",
      "",
      "",
      1,
      "",
      6
    ).row;
    // console.log(that.Six_major_advantages, " that.Six_major_advantages");

    var syanTemp = that.Base.GetNewsInfoByCid("", that.api.syan).row;
    that.syanList.btn1 = syanTemp[0];
    that.syanList.btn2 = syanTemp[1];
    that.syanList.btn3 = syanTemp[2];
    that.syanList.btn4 = syanTemp[3];

    that.zyszList = that.Base.GetNewsInfoByCid(
      "",
      that.api.zysz,
      "",
      "",
      "",
      1,
      "",
      30
    ).row;

    function browserRedirect() {
      var sUserAgent = navigator.userAgent.toLowerCase();
      var bIsIpad = sUserAgent.match(/ipad/i) == "ipad";
      var bIsIphoneOs = sUserAgent.match(/iphone os/i) == "iphone os";
      var bIsMidp = sUserAgent.match(/midp/i) == "midp";
      var bIsUc7 = sUserAgent.match(/rv:1.2.3.4/i) == "rv:1.2.3.4";
      var bIsUc = sUserAgent.match(/ucweb/i) == "ucweb";
      var bIsAndroid = sUserAgent.match(/android/i) == "android";
      var bIsCE = sUserAgent.match(/windows ce/i) == "windows ce";
      var bIsWM = sUserAgent.match(/windows mobile/i) == "windows mobile";
      if (
        bIsIpad ||
        bIsIphoneOs ||
        bIsMidp ||
        bIsUc7 ||
        bIsUc ||
        bIsAndroid ||
        bIsCE ||
        bIsWM
      ) {
        //跳转移动端页面
      } else {
        //跳转pc端页面
        new WOW().init();
      }
    }
    browserRedirect();

    // console.log(that.Base.GetNewsInfoByCid("", 4016));
    // 考生关注

    // 校园风光
    // xyfgF();
    function xyfgF() {
      var listTemp = 0;
      var listLastTemp = 0;
      $(".xyfg-itemBox .xyfg-item").eq(0).addClass("on").fadeIn();
      $(".xyfg-cont dl dd").hover(function () {
        listTemp = 0;
        listLastTemp = 0;
        $(this).addClass("on").siblings().removeClass("on");
        $(".xyfg-itemBox .xyfg-item")
          .eq($(this).index())
          .addClass("on")
          .fadeIn()
          .siblings()
          .removeClass("on")
          .fadeOut();
      });
    }

    $(".xyyw .imgBox img").attr(
      "src",
      $(".xwzx-cont .xyyw ul li").attr("data-fmimg")
    );
    that.xcpList = that.Base.GetFilesInfoByCid(
      "",
      that.api.hdsg,
      "",
      "",
      1,
      "",
      10
    ).GetData;

    setTimeout(function () {
      $(".video_box ul li").eq(1).addClass("on");
    });
    $(document).ready(function () {
      $(function () {
        var tower_click_Flag = true;
        if (that.xcpList.length >= 4) {
          // $(window).on("click", ".tower-btn-prev", () => {
          //   console.log("1");
          //   videoMoveF(false);
          // });
          // $(window).on("click", ".tower-btn-next", () => {
          //   console.log("2");
          //   videoMoveF(true);
          // });

          $(".tower-btn-prev").click(() => {
            // console.log("1");
            if (tower_click_Flag) {
              tower_click_Flag = false;
              videoMoveF(false);
            }
          });
          $(".tower-btn-next").click(() => {
            // console.log("2");
            if (tower_click_Flag) {
              tower_click_Flag = false;
              videoMoveF(true);
            }
          });

          function videoMoveF(tower) {
            if (tower) {
              $(".video_box ul li").css({ left: "1200px" });
              $(".video_box ul li")
                .eq(0)
                .stop()
                .css({ left: "0px", "z-index": "1" })
                .animate({ left: "-631px" });
              $(".video_box ul li")
                .eq(1)
                .stop()
                .css({ left: "280px", "z-index": "2" })
                .animate({ left: "0px" });
              $(".video_box ul li")
                .eq(2)
                .stop()
                .css({ left: "631px", "z-index": "9" })
                .animate({ left: "280px" })
                .addClass("on")
                .siblings()
                .removeClass("on");
              $(".video_box ul li")
                .eq(3)
                .stop()
                .css({ left: "1200px", "z-index": "2" })
                .animate({ left: "631px" }, function () {
                  $(".video_box ul li").eq(0).appendTo($(".video_box ul"));
                  setTimeout(function () {
                    tower_click_Flag = true;
                  });
                });
            } else {
              $(".video_box ul li").css({ left: "1200px" });
              $(".video_box ul li").eq(-1).prependTo($(".video_box ul"));
              setTimeout(() => {
                $(".video_box ul li")
                  .eq(1)
                  .addClass("on")
                  .siblings()
                  .removeClass("on");
                $(".video_box ul li")
                  .eq(0)
                  .stop()
                  .css({ left: "-631px", "z-index": "2" })
                  .animate({ left: "0px" });
                $(".video_box ul li")
                  .eq(1)
                  .stop()
                  .css({ left: "0px", "z-index": "9" })
                  .animate({ left: "280px" });
                $(".video_box ul li")
                  .eq(2)
                  .stop()
                  .css({ left: "280px", "z-index": "2" })
                  .animate({ left: "631px" });
                $(".video_box ul li")
                  .eq(3)
                  .stop()
                  .css({ left: "631px", "z-index": "1" })
                  .animate({ left: "1200px" }, function () {
                    setTimeout(function () {
                      tower_click_Flag = true;
                    });
                  });
              });
            }
          }
        }
      });
    });
    // $(window).load(function() {

    // });
  },
  methods: {
    Navformat: function (data) {
      var obj = data;
      // console.log(obj);
      var temp = [];
      for (var i = 0; i < obj.length; i++) {
        if (obj[i].FID == "0") {
          var main = obj[i];
          main.Clist = [];
          for (var j = 0; j < obj.length; j++) {
            if (obj[j].FID == main.ID) {
              main.Clist.push(obj[j]);
            }
          }
          temp.push(main);
        }
      }
      return temp;
    },
    openzxbm: function () {
      this.$store.dispatch("showAddStudent");
    },
    listFormat: function (id, len) {
      var that = this;
      var towerData = [];
      towerData = that.Base.GetFilesInfoByCid(
        "",
        id,
        "",
        "",
        1,
        "",
        50
      ).GetData;
      var result = [];
      for (var i = 0; i < towerData.length; i += len) {
        result.push(towerData.slice(i, i + len));
      }
      return result;
      // console.log(result)
    },
    openVideo: function (obj) {
      var that = this;
      that.videoFlag = true;
      that.xcpPalyActive = obj;
    },
  },
};
</script>
