.bc-4b9bf5 {
  background-color: #4b9bf5;
}
.bc-ffa040 {
  background-color: #ffa040;
}
.bc-c82e3b {
  background-color: #c82e3b;
}
.video_mk {
  width: 100%;
  height: 638px;
  background: url(../assets/img/video_mk_bg.jpg) center no-repeat;
  overflow: hidden;
}
.video_mk .indexTitle {
  margin-top: 70px;
}
.video_mk .indexTitle span {
  color: white;
}
.video_mk .indexTitle span::before {
  background: url(../assets/img/indexTitle_leftIcon_w.png) center no-repeat;
}
.video_mk .indexTitle span::after {
  background: url(../assets/img/indexTitle_rightIcon_w.png) center no-repeat;
}
.video_mk .video_box {
  width: 100%;
  height: 320px;
  max-width: 1600px;
  margin: 0 auto;
  overflow: hidden;
  position: relative;
  margin-top: 87px;
}
.video_mk .video_box ul {
  position: absolute;
  width: 1200px;
  height: 320px;
  left: 50%;
  margin-left: -600px;
  overflow: hidden;
}
.video_mk .video_box li {
  width: 569px;
  height: 100%;
  position: absolute;
  left: -1200px;
  top: 0px;
  z-index: 1;
  transform: scale(0.8);
  transition: transform 0.4s;
  cursor: pointer;
}
.video_mk .video_box li::before {
  content: "";
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  position: absolute;
  top: 0;
  left: 0;
  pointer-events: none;
}
.video_mk .video_box li::after {
  content: "";
  width: 63px;
  height: 63px;
  background: url(../assets/img/videoPlayBtn.png) center no-repeat;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
}
.video_mk .video_box li:nth-child(1) {
  left: 0px;
  z-index: 2;
}
.video_mk .video_box li:nth-child(2) {
  left: 280px;
  z-index: 9;
}
.video_mk .video_box li:nth-child(3) {
  left: 631px;
  z-index: 2;
}
.video_mk .video_box li:nth-child(4) {
  left: 1200px;
  z-index: 1;
}
.video_mk .video_box li a {
  display: block;
  width: 100%;
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.video_mk .video_box li.on {
  transform: scale(1);
  /* z-index: 9 !important; */
}
.video_mk /deep/ .tower-btn {
  width: 100%;
  position: absolute;
  width: 1200px;
  height: 0px;
  top: 137px;
  left: 50%;
  margin-left: -600px;
  z-index: 9;
}
.video_mk /deep/ .tower-btn-prev,
.video_mk /deep/ .tower-btn-next {
  display: block;
  width: 30px;
  height: 65px;
  position: absolute;
  cursor: pointer;
}
.video_mk /deep/ .tower-btn-prev {
  left: 0px;
  background: url(../assets/img/video_left_btn.png) center no-repeat;
}
.video_mk /deep/ .tower-btn-next {
  right: 0px;
  background: url(../assets/img/video_right_btn.png) center no-repeat;
}
/*为啥选着五月花 */
.why {
  margin-top: 151px;
}
</style>




