<template>
  <div class="jnpx">
    <ul class="clearfix">
      <li v-for="i in pageList" :key="i.ID">
        <router-link :to="{ path: '/list/content', query: { id: i.ID } }">
          <div class="imgBox"><img :src="i.Cover" alt="" /></div>
          <div class="font">
            <h3>{{ i.Title }}</h3>
            <p>{{ i.Describe }}</p>
          </div>
        </router-link>
      </li>
    </ul>
    <pageMove @pageindex="pageF" :pageOption="pageOption"></pageMove>
  </div>
</template>

<script>
export default {
  name: "jnpx",
  data() {
    return {
      pageText: "",
      pageList: [],
      pageOption: {
        pageIndex: 1,
        pageTotal: 10,
        pageSize: 6,
      },
    };
  },
  created() {
    this.pathList = this.$route.query;
    this.getPageList();
    console.log(this.pathList);
  },
  methods: {
    pageF(data) {
      this.pageOption.pageIndex = data;
      this.getPageList();
    },
    getPageList() {
      var temp = this.Base.GetNewsInfoByCid(
        "",
        this.pathList.cid,
        "",
        "",
        "",
        this.pageOption.pageIndex,
        "",
        this.pageOption.pageSize
      );

      this.pageList = temp.row;
      this.pageOption.pageTotal = temp.total;
    },
  },
};
</script>

<style scoped>
ul {
  padding-bottom: 15px;
  width: 1215px;
  min-height: 774px;
}
li {
  width: 390px;
  height: 280px;
  position: relative;
  margin-bottom: 107px;
  float: left;
  margin-right: 15px;
}
li .imgBox {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
li .imgBox img {
  transition: all 0.4s;
}
li .font {
  width: 340px;
  height: 115px;
  position: absolute;
  bottom: -76px;
  left: 50%;
  margin-left: -170px;
  background-color: white;
  box-shadow: 0px 0px 16px 0px rgba(43, 60, 198, 0.2);
  transition: all 0.4s;
}
li .font h3 {
  width: 210px;
  height: 50px;
  background-color: #4273ff;
  border-radius: 10px;
  font-size: 24px;
  line-height: 50px;
  text-align: center;
  color: white;
  position: absolute;
  left: 50%;
  top: -25px;
  margin-left: -105px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  padding: 0px 10px;
}
li .font p {
  width: 308px;
  height: 57px;
  text-align: justify;
  margin: 0 auto;
  margin-top: 40px;
  color: #999999;
  line-height: 1.2;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  overflow: hidden;
}
li:hover .imgBox img {
  transform: scale(1.1);
}
li:hover .font {
  transform: translateY(-10px);
}
</style>



